.benefits{
    position: relative;
    margin: 0 auto;
    max-width: 1262px;
    box-shadow: 0px 2px 20px rgba(21, 24, 36, 0.15993);
}

.benefits__title{
    width: 100%;
    font-size: 35px;
    padding: 93px 100px 88px 9%;
    position: absolute;
    top: 0;
    left: 0;
    transform: translateY(-100%);
    background-color: #fff;
  }

@media (max-width:1280px){
    .benefits__title{
     padding: 93px 100px 88px 17%;
    }
}

@media (max-width:800px){
    .benefits__title{
     padding: 16% 10% 16% 10%;
     text-align: center;
     font-size: 26px;
    }
}